@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.home-container {
  height: 100vh;
}

.video-container {
  position: fixed;
  z-index: 0;
}

@media (min-aspect-ratio: 16/9) {
  .video-container {
    width: 100%;
    height: auto;
  }
}

@media (max-aspect-ratio: 16/9) {
  .video-container {
    width: auto;
    height: 100%;
  }
}

.afterEnded {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.afterEnded-image {
  /* transform: translateX(-20%); */
  /* position: relative;
    max-width: 100%; */
  text-align: center;
  -webkit-animation: coffee 2s ease-in-out;
          animation: coffee 2s ease-in-out;
  /* other keywords like "infinite alternate" can be useful here */
}

@-webkit-keyframes coffee {
  /* You could think of as "step 1" */
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  /* You could think of as "step 2" */
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes coffee {
  /* You could think of as "step 1" */
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  /* You could think of as "step 2" */
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.afterEnded-text {
  font-family: 'Bebas Neue', cursive;
  font-size: 128px;
  color: #634832;
  justify-self: center;
  -webkit-animation: title 2s ease-in-out;
          animation: title 2s ease-in-out;
  /* other keywords like "infinite alternate" can be useful here */
}

.afterEnded-text-subtitle {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-size: 28px;
}

.italic {
  font-style: italic;
  font-size: 24px;
  padding: 16px;
}

@-webkit-keyframes title {
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes title {
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.about-container {
  display: flex;
  justify-content: left;

}

.about-container-wrapper {
  overflow: hidden;
  height: 100vh;
  padding-top: 80px;
  background-color: rgba(var(--main-dark-rgb), 0.1);
}

.about-title {
  font-family: 'Bebas Neue', cursive;
  font-size: 72px;
  color: var(--main-dark)
}

.about-title-container {
  width: 30%;
  align-self: center;
}

.about-text {
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  color: var(--main-dark);
}

.about-container-bottom {
  display: flex;
  justify-content: flex-end;
  margin: 10% 20px 0 0;
}
.variant-container {
  padding-top: 80px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.variant-card-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.card {
  color: rgb(236, 224, 209);
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  background-color: rgba(150, 114, 89, 0.9);
}

.card-shadow {
  box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.02),
    0 6.7px 5.3px rgba(0, 0, 0, 0.028),
    0 12.5px 10px rgba(0, 0, 0, 0.035),
    0 22.3px 17.9px rgba(0, 0, 0, 0.042),
    0 41.8px 33.4px rgba(0, 0, 0, 0.05),
    0 100px 80px rgba(0, 0, 0, 0.07);
}

.card-title {
  font-family: 'Bebas Neue', cursive;
  font-size: 28px;
  text-align: center;
}

.card-content {
  font-family: 'Montserrat', sans-serif;
  text-align: justify;
  font-style: italic;
  text-indent: 30px;
  line-height: 30px;
}
.contact-container {
  padding-top: 8%;
  display: flex;
  justify-content: space-between;
  align-self: center;
}

.contact-container-wrapper {
  height: 100vh;
  background-color: rgba(var(--main-dark-rgb), 0.1);
  padding-top: 80px;
  overflow: hidden;
}

.contact-image-container {
  align-self: center;
}

.contact-card {
  padding: 40px;
  color: var(--main-dark);
  justify-self: center;

}

.contact-card-title {
  font-family: 'Bebas Neue', cursive;
  font-size: 84px;

}

.social-media-icons {
  width: 100%;
  padding-top: 10%;
  font-size: 48px;
  display: flex;
  justify-content: space-around;
}

.contact-card-subtitle>div {
  font-family: 'Montserrat', sans-serif;
  font-size: 36px;
  font-weight: bold;
  padding-top: 40%;
}

.contact-card-subtitle>div>div {
  font-size: 18px;
  font-weight: regular;
}

.contact-card-subtitle {
  display: flex;
  justify-content: space-between;
}
.header-wrapper {
  height: 80px;
  position: fixed;
  top: 0;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  z-index: 999;
}
.navbar {
  display: flex;
  justify-content: space-between;
}

.navbar-container {}

.navbar-brand {
  margin: 0px 0 0 10%;
}

.navbar-menu {
  width: 25%;
}

.navbar-menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

a:-webkit-any-link {
  font-weight: 1000;
  text-decoration: none;
  color: var(--main-dark);
}

.btn-1 {
  letter-spacing: 0.05rem;
  transition: 0.5s all ease-in-out;
  position: relative;
  background: transparent;
}

.btn-1:before {
  border-top: 0.1rem solid#634832;
  border-bottom: 0.1rem solid #634832;
  display: block;
  position: absolute;
  background: transparent;
  content: '';
  top: -10px;
  bottom: -10px;
  left: 200%;
  right: 200%;
  transition: 0.48s all ease-in-out;
}

.btn-1:hover {
  letter-spacing: 0.4rem;
  font-weight: 1200;
}

.btn-1:hover:before {
  left: 0;
  right: 0;
}
body {
    margin: 0;
}

html,
body,
#root,
    {
    height: 100%;
}

.app-container {
    background-color: rgba(236, 224, 209);
    height: 100%;
}

:root {
    --main-dark: #634832;
    --main-dark-rgb: 99, 72, 50;
    --main-light: rgb(236, 224, 209)
}
